@import '../../variables';

.main-page {
	background: $gray;

	.container {
		padding: 11vh 0 1vh 0;
		min-height: 100vh;
		max-width: calc(100vw - 2vw);
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
		row-gap: 10px;
		justify-items: center;

		@media (max-width: 900px) {
			padding: 6vh 0 0 0;
		}
	}
}
