@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap');
@import './variables';

* {
	font-family: 'Open Sans', sans-serif;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

ul,
li,
dl {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

.container {
	margin: 0 auto;
}

.spinner {
	margin-top: 25vh;
}
